import { Stack, Typography } from '@mui/material'
import { DataGrid } from '@mui/x-data-grid'
import { format } from 'date-fns'
import { useEffect } from 'react'
import { Link } from 'react-router-dom'
import { useAppDispatch, useAppSelector } from 'redux/hooks'
import { getFormAnswers } from 'redux/slices/FormAnswer'

export default function Page() {
	const dispatch = useAppDispatch()
	const tokenInfo = useAppSelector((s) => s.UI.tokenInfo)
	const status = useAppSelector((s) => s.FormAnswer.status)
	const list = useAppSelector((s) => s.FormAnswer.list)
	useEffect(() => {
		if (status === 'init') dispatch(getFormAnswers())
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [])

	if (!tokenInfo || !list) return null

	return (
		<Stack gap={2}>
			<Stack direction="row" alignItems="center" justifyContent="space-between" gap={2} sx={{}}>
				<Typography
					sx={{
						fontWeight: 600,
					}}
					variant="h5"
				>
					フォーム回答一覧
				</Typography>
			</Stack>
			<DataGrid
				rows={list}
				columns={
					[
						{
							field: 'name',
							headerName: 'フォーム名',
							flex: 1,
							renderCell: (v) => {
								return (
									<Link to={`/form-answer/${v.row.formId}`}>
										<Typography variant="caption" mr={1}>
											{v.row.formName}
										</Typography>
									</Link>
								)
							},
						},
						{
							field: 'count',
							headerName: '回答数',
							width: 70,
							align: 'center',
							renderCell: (v) => {
								return (
									<Typography variant="caption" mr={1}>
										{v.row.answers.length}
									</Typography>
								)
							},
						},
						{
							field: 'start',
							headerName: '開始日',
							width: 120,
							renderCell: (v) => {
								return (
									<Typography variant="caption" mr={1}>
										{v.row.startDate ? format(new Date(v.row.startDate), 'yyyy年MM月dd日') : '--'}
									</Typography>
								)
							},
						},
						{
							field: 'end',
							headerName: '終了日',
							width: 120,
							renderCell: (v) => {
								return (
									<Typography variant="caption" mr={1}>
										{v.row.startDate ? format(new Date(v.row.startDate), 'yyyy年MM月dd日') : '--'}
									</Typography>
								)
							},
						},
					]
				}
				initialState={{
					pagination: {
						paginationModel: { page: 0, pageSize: 10 },
					},
				}}
				getRowId={v=>v.formId}
				pageSizeOptions={[10, 20, 30, 50]}
				sx={{
					'& .MuiDataGrid-cell:focus': {
						outline: 'none',
					},
					'& .MuiDataGrid-cell:focus-within': {
						outline: 'none',
					},
					'& .MuiDataGrid-cell:hover': {},
					'& .MuiDataGrid-columnHeaders': {
						bgcolor: 'primary.dark',
						color: '#fff',
					},
					'& .MuiDataGrid-columnHeaders svg': {
						color: '#fff',
					},
				}}
				density="compact"
				hideFooterSelectedRowCount
				disableColumnMenu
				disableRowSelectionOnClick
			/>
		</Stack>
	)
}
